import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
    components: {
        XHeader,
        dateFormat,
        Toast,
        XButton,
        XInput,
        Checker,
        CheckerItem,
        Group,
        XTextarea,
        Datetime,
        Actionsheet
    },
    data() {
        return {
            infoValue: '',//活动介绍
            startTime: '',
            endTime: '',
            memberNum: 0,//报名人数
            area: '',
            show: false,
            menus: {
                take: '拍照',
                select: '从相册选择'
            },
        }
    },
    methods: {
        take() {
            console.log("拍照")
        },
        select() {
            console.log("相册");
        }
    }
}




